.app {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

/* .main {
  flex-basis: 40%;
  display: flex;
  flex-direction: row;
} */

.map {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 100vh;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
}


.search {
  padding: 10px;
  margin-right: 5px;
}

.search input {
  height: 30px;
  font-size: 20px;
  padding-left: 5px;
  color: #999;
  width: 100%;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.piechart {
  width: 100%;
}



